@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", "微軟正黑體";
}

.footer a,
.nav__list a {
  display: block;
}

.footer,
.nav,
.nav__list {
  display: flex;
}

.nav h1,
.nav__list a,
.footer h2,
.footer a,
.navbar {
  text-decoration: none;
  padding: 1em 1em;
  font-size: 1rem;
  color: #fff;
}

.nav__list a,
.footer a,
.navbar {
  cursor: pointer;
}

.nav__list a:hover, .nav__list a:focus,
.footer a:hover,
.footer a:focus,
.navbar:hover,
.navbar:focus {
  background-color: #333;
}

.nav,
.footer {
  background-color: #444;
}

.nav {
  flex-wrap: wrap;
  justify-content: space-between;
}

.footer {
  text-align: center;
  justify-content: center;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}
